import request from "./http";

// 获取用戶名密碼验证
export function getLoginCellphone(params){
    return request({
        url:"/login/cellphone",
        method:"post",
        // 注意:post 使用data
        // 注意:get 使用params
        data:params
    })
}

// 获取验证码
export function getCaptchaSent(params){
    return request({
        url:"/captcha/sent",
        method:"get",
        params
    })
}
// 获取验证验证码
export function getCaptchaVerify(params){
    return request({
        url:"/captcha/verify",
        method:"get",
        params
    })
}
// 邮箱验证
// /login?email=xxx@163.com&password=yyy

export function getemail(params){
    return request({
        url:"/login",
        method:"get",
        params:{
            ...params
        }
    })
}

// 用户注册
// /register/cellphone?phone=13xxx&password=xxxxx&captcha=1234&nickname=binary1345

export function getregister(params){
    return request({
        url:"/register/cellphone",
        method:"get",
        params:{
            ...params
        }
    })
}