<template>
  <div class="mains">
    <div class="login">
      <div class="return">
        <van-icon
          @click="$router.push('/recommend').catch(() => {})"
          name="arrow-left"
        />
      </div>
      <h1>欢迎登录</h1>
      <div class="form">
        <!-- 手机号码 -->
        <van-field
          v-model="phone"
          type="tel"
          label="手机号"
          placeholder="请输入手机号码"
          class="item"
          v-show="type == 1"
        />
        <van-field
          v-model="password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          class="item"
          v-show="type == 1"
        />

        <!-- 邮箱登入 -->
        <van-field
          v-model="mail"
          type="mail"
          label="邮箱"
          placeholder="请输入邮箱"
          class="item"
          v-show="type == 2"
        />
        <van-field
          v-model="password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          class="item"
          v-show="type == 2"
        />

        <!-- 注册 -->
        <van-field
          v-model="nickname"
          type="text"
          label="昵称"
          placeholder="请输入昵称"
          class="item"
          v-show="type == 3"
        />
        <van-field
          v-model="phone"
          type="tel"
          label="手机号"
          placeholder="请输入手机号码"
          class="item"
          v-show="type == 3"
        />
        <van-field
          v-model="password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          class="item"
          v-show="type == 3"
        />
        <van-field
          v-model="code"
          center
          clearable
          label="短信验证码"
          placeholder="请输入短信验证码"
          class="item"
          v-show="type == 3"
        >
          <template #button>
            <van-button class="but" size="mini" type="primary" @click="getCode"
              >发送</van-button
            >
          </template>
        </van-field>

        <div class="item">
          <div class="submit" @click="submit">提交</div>
        </div>
      </div>
      <div class="type">
        <p @click="type = 1" v-show="type != 1">手机号登入</p>
        <p @click="type = 2" v-show="type != 2">邮箱登入</p>
        <p @click="type = 3" v-show="type != 3">注册</p>
      </div>
    </div>
